import React, { useEffect, useMemo, useState } from 'react'
import { CustomTable, CustomTabs } from '../../reuseableComponents'
import { Icon } from '../../commanComponent';
import { useDispatch, useSelector } from 'react-redux';
import { pagination } from '../../../utils/Constants.config';
import { leaveAction, travelAndClaimAction } from '../../../core/resource/actions';
import CommonApproveRejectModal from '../../modal/CommonApproveRejectModal';
import ProgressModel from '../../leave_management/ProgressModel';
import { activeTabPermission, dateFormate, getBadgeColor, isPermission } from '../../../utils/common';
import Image from '../../commanComponent/Image';
import ViewTravelDetail from './ViewTravelDetail';
import ViewBillDetail from '../claim/ViewBillDetail';
import ViewTravelClaimDetail from '../claim/ViewTravelClaimDetail';

const TravelApprovalRequest = () => {
  const dispatch = useDispatch()
  const {

    getTravelApprovalPending,
    getTravelApprovalRes,
    getTravelApprovalError,

    travelRequestApproveRejectPending,
    travelRequestApproveRejectRes,
    travelRequestApproveRejectError,

    getClaimApprovalPending,
    getClaimApprovalRes,
    getClaimApprovalError,

    approveRejectClaimPending,
    approveRejectClaimRes,
    approveRejectClaimError,
  } = useSelector(state => state.travelAndClaim)

  const {


    getLeaveProgressPending,
    getLeaveProgressRes,
    getLeaveProgressError,
  } = useSelector(state => state.leave);
  const { employeeDetailsData } = useSelector(state => state?.employee)
  const travelApprovalPer =isPermission("travel_approval")
  const claimApprovalPer =isPermission("claim_approval")
  const [currentPage, setCurrentPage] = useState(1)
  const [travelApprovalData, setTravelApprovalData] = useState('')
  const [approveRejectTravelId, setApproveRejectTravelId] = useState('')
  const [isOpenApproveRejectModal, setIsOpenApproveRejectModal] = useState(false)
  const [isOpenApproveRejectClaimModal, setIsOpenApproveRejectClaimModal] = useState(false)
  const [approveRejectComment, setApproveRejectComment] = useState('')
  const [progressData, setProgressData] = useState('')
  const [openProgressModel, setOpenProgressModel] = useState(false)
  const [travelClaimApprovalData, setTravelClaimApprovalData] = useState('')
  const [currentPageClaim, setCurrentPageClaim] = useState(1)
  const [approveRejectTravelClaimComment, setApproveRejectTravelClaimComment] = useState('')
  const [approveRejectTravelClaimId, setApproveRejectTravelClaimId] = useState('')
  const [selectedTravelData, setSelectedTravelData] = useState('')
  const [isOpenTravelDetailModal, setIsOpenTravelDetailModal] = useState(false)
  const [isOpenBillDetailModal, setIsOpenBillDetailModal] = useState(false)
  const [selectedTravelClaimData, setSelectedTravelClaimData] = useState('')
  const [isOpenTravelClaimDetailModal,setIsOpenTravelClaimDetailModal]=useState(false)
  const fields = [
    { key: 'sn', label: 'S.N' },
    { key: 'name', label: 'Name' },
    { key: 'emp_id', label: 'Employee Code' },
    { key: 'trave_type', label: 'Trave Type' },
    { key: 'purpose_of_travel', label: 'Business Purpose' },
    { key: 'action_type', label: 'Status' },
    // { key: 'travel_detail', label: 'Travel Detail' },
    { key: 'action', label: 'Action' }
  ]
  const travelAndClaimApprovalRequest = [
    { name: "Travel Request", id: "travel_request",permissionId:"travel_approval_requests" },
    { name: "Claim Request", id: "claim_request" ,permissionId:"claim_approval_request" },
  ]
  const activeTravelClaimAppReqTab =activeTabPermission(travelAndClaimApprovalRequest)


  const scopedSlots = useMemo(() => ({
    sn: (_, index) => (
      <span>{index + 1}</span>
    ),
    action_type: item => (
      <span
        className={`badge ${getBadgeColor(item.action_type)}`}
      >
        {item.action_type}
      </span>
    ),
    // travel_detail: item => (

    // ),

    action: item => (
      <div className="btn-group actionDrp gap-3">
        <span onClick={() => toggleTravelDetail(item)} >
          <img src="images/view-icon.svg" alt width="32" />
        </span>
        <span style={{ cursor: "pointer" }}
          onClick={() => onHandleProgressModel("travel", item?.travel_id)}>

          <Image imgSrc={"images/info-orange-bg.svg"} altText={""} width={32}></Image>
        </span>
        {travelApprovalPer &&<div className="button-container">
          {item.action_type == 'pending' && item?.approval_action_to?.split(',').includes(employeeDetailsData?.id) &&
          <span
            onClick={() => toggleApprovalRejectModal(item?._id)}
          >
            <Image imgSrc={"images/nextaction-Icon.svg"} altText={""} width={32}></Image>
          </span>}

        </div>}
      </div>

    )
  }), [travelApprovalData]);

  const toggleTravelDetail = (data) => {
    setIsOpenTravelDetailModal(!isOpenTravelDetailModal)
    setSelectedTravelData(data)
  }

  useEffect(() => {
    getTravelApprovalData()
  }, [currentPage])
  const getTravelApprovalData = () => {
    const payload = {
      page: currentPage,
      limit: pagination.limit
    }
    dispatch(travelAndClaimAction.getTravelApprovalRequest(payload));
  }

  useEffect(() => {
    if (!getTravelApprovalPending) {
      if (getTravelApprovalError) {
        return;
      }
      if (getTravelApprovalRes) {
        if (getTravelApprovalRes?.code === 200) {
          setTravelApprovalData(getTravelApprovalRes?.data)
        }
      }
    }
  }, [
    getTravelApprovalPending,
    getTravelApprovalRes,
    getTravelApprovalError,
  ]);

  /* Toggle Approve Reject Modal */
  const toggleApprovalRejectModal = (id) => {
    if (id) {
      setApproveRejectTravelId(id)
    }
    setIsOpenApproveRejectModal(!isOpenApproveRejectModal)
  }
  const approveRejectTravel = (type) => {
    if (type) {
      const params = {
        _id: approveRejectTravelId,
        action_type: type,
        comment: approveRejectComment
      }
      dispatch(travelAndClaimAction?.travelRequestApproveRejectRequest(params))
    } else {
      toggleApprovalRejectModal()
    }
  }
  useEffect(() => {
    if (!travelRequestApproveRejectPending) {
      if (travelRequestApproveRejectError) {
        dispatch(travelAndClaimAction.handleClearTravelStore("HANDLE_CLEAR_TRAVEL_REQUEST_APPROVE_REJECT_PENDING_STORE"))
        return;
      }
      if (travelRequestApproveRejectRes) {
        if (travelRequestApproveRejectRes?.code === 200) {
          toggleApprovalRejectModal()
          dispatch(travelAndClaimAction.handleClearTravelStore("HANDLE_CLEAR_TRAVEL_REQUEST_APPROVE_REJECT_PENDING_STORE"))
          getTravelApprovalData()
        }
      }
    }
  }, [
    travelRequestApproveRejectPending,
    travelRequestApproveRejectRes,
    travelRequestApproveRejectError,
  ]);

  /* approval process b*/
  const onHandleProgressModel = (type, id) => {
    if (type || id) {
      const payload = {
        id: id || '',
        type: type || ''
      }
      dispatch(leaveAction.getLeaveProgressRequest(payload))
    }

    setOpenProgressModel(!openProgressModel)

  }
  useEffect(() => {
    if (!getLeaveProgressPending) {
      if (getLeaveProgressError) {
        return;
      }
      if (getLeaveProgressRes) {
        if (getLeaveProgressRes?.code === 200) {
          setProgressData(getLeaveProgressRes?.data)
        }
      }
    }
  }, [
    getLeaveProgressRes,
    getLeaveProgressError,
    getLeaveProgressPending
  ]);


  /* -----------------------------------------Claim Approval Section ------------------------------*/
  const scopedSlotsClaimApproval = useMemo(() => ({
    sn: (_, index) => (
      <span>{index + 1}</span>
    ),

    action_type: item => (
      <span
        className={`badge ${getBadgeColor(item.action_type)}`}
      >
        {item.action_type}
      </span>
    ),
    travel_details: item => (
      <span
        onClick={() => toggleTravelClaimDetail(item)}
      >
        <img src="images/view-icon.svg" alt width="32" />
      </span>
    ),
    created_at: item => (
      <span >
        {dateFormate(item?.created_at)}
      </span>
    ),

    action: item => (
      <div className="btn-group actionDrp gap-2">
        <span style={{ cursor: "pointer" }}
          onClick={() => onHandleProgressModel("claim", item?.claim_id)}>
          <Image imgSrc={"images/info-orange-bg.svg"} altText={""} width={32}></Image>
        </span>
        {claimApprovalPer &&<div className="button-container">{item.action_type == 'pending' && item?.approval_action_to?.split(',').includes(employeeDetailsData?.id) &&
          <span
            onClick={() => toggleApprovalRejectModal(item?._id)}
          >
            <Image imgSrc={"images/nextaction-Icon.svg"} altText={""} width={32}></Image>
          </span>}

        </div>}
      </div>

    )
  }), [travelApprovalData]);


  const claimApprovalFields = [
    { key: 'sn', label: 'S.N' },
    { key: 'travel', label: 'Travel' },
    { key: 'emp_id', label: 'Employee Code' },
    { key: 'name', label: 'Name' },
    { key: 'action_type', label: 'Status' },
    { key: 'created_at', label: 'Created Date' },
    { key: 'travel_details', label: 'Claim Detail' },
    { key: 'action', label: 'Action' }
  ]

  useEffect(() => {
    getClaimApprovalData()
  }, [currentPageClaim])

  const getClaimApprovalData = () => {
    const payload = {
      page: currentPageClaim,
      limit: pagination.limit
    }
    dispatch(travelAndClaimAction.getClaimApprovalRequest(payload));
  }

  useEffect(() => {
    if (!getClaimApprovalPending) {
      if (getClaimApprovalError) {
        return;
      }
      if (getClaimApprovalRes) {
        if (getClaimApprovalRes?.code === 200) {
          setTravelClaimApprovalData(getClaimApprovalRes?.data)
        }
      }
    }
  }, [
    getClaimApprovalPending,
    getClaimApprovalRes,
    getClaimApprovalError,
  ]);


  const toggleApprovalRejectTravelClaimModal = (id) => {
    if (id) {
      setApproveRejectTravelClaimId(id)
    }
    setIsOpenApproveRejectClaimModal(!isOpenApproveRejectClaimModal)
  }
  const approveRejectTravelClaim = (type) => {
    if (type) {
      const params = {
        _id: approveRejectTravelClaimId,
        action_type: type,
        comment: approveRejectTravelClaimComment
      }
      dispatch(travelAndClaimAction?.travelRequestApproveRejectRequest(params))
    } else {
      toggleApprovalRejectTravelClaimModal()
    }
  }
  useEffect(() => {
    if (!approveRejectClaimPending) {
      if (approveRejectClaimError) {
        dispatch(travelAndClaimAction.handleClearTravelStore("HANDLE_CLEAR_APPROVE_REJECT_CLAIM_STORE"))
        return;
      }
      if (approveRejectClaimRes) {
        if (approveRejectClaimRes?.code === 200) {
          toggleApprovalRejectTravelClaimModal()
          dispatch(travelAndClaimAction.handleClearTravelStore("HANDLE_CLEAR_APPROVE_REJECT_CLAIM_STORE"))
          getClaimApprovalData()
        }
      }
    }
  }, [
    approveRejectClaimPending,
    approveRejectClaimRes,
    approveRejectClaimError,
  ]);

  const toggleBillDetail = (data) => {
    if (data) {
      setSelectedTravelClaimData(data)
    } else {
      setSelectedTravelClaimData('')
    }
    setIsOpenBillDetailModal(!isOpenBillDetailModal)

  }

  const toggleTravelClaimDetail=(data)=>{
    if(data){
        setSelectedTravelClaimData(data)
    }else{
        setSelectedTravelClaimData('')
    }
    setIsOpenTravelClaimDetailModal(!isOpenTravelClaimDetailModal)

    }
  return (
    <div className='cardBox myRequest mb-3 employeeHistory'>

      <div className="cardHead d-flex">
        <img src="images/myrequest-icon.svg" alt="" width={35} />
        <h5 className="ms-2 ">Approval Request</h5>
      </div>

      <CustomTabs tabs={travelAndClaimApprovalRequest} type="travelAndClaimApprovalRequest" />
      <div className="tab-content">
        <div className={`tab-pane fade ${activeTravelClaimAppReqTab==="travel_request"&&"active show"}`} id="travel_request">
          <CustomTable
            data={travelApprovalData?.data}
            fields={fields}
            isLoading={getTravelApprovalPending}
            pagination={true}
            currentPage={currentPage}
            setCurrentPage={setCurrentPage}
            totalPages={travelApprovalData?.totalPages}
            isHeader={false}
            tableStyle={{ padding: "0px", boxShadow: "none" }}
            scopedSlots={scopedSlots}
          />
        </div>
        <div className={`tab-pane fade ${activeTravelClaimAppReqTab==="claim_request"&&"active show"}`} id="claim_request">
          <CustomTable
            data={travelClaimApprovalData?.data}
            fields={claimApprovalFields}
            isLoading={getClaimApprovalPending}
            pagination={true}
            currentPage={currentPageClaim}
            setCurrentPage={setCurrentPageClaim}
            totalPages={travelClaimApprovalData?.totalPages}
            isHeader={false}
            tableStyle={{ padding: "0px", boxShadow: "none" }}
            scopedSlots={scopedSlotsClaimApproval}
          />

        </div>
      </div>

      {isOpenApproveRejectModal && <CommonApproveRejectModal
        title={'Travel Approve Reject'}
        onApproveReject={approveRejectTravel}
        isShow={isOpenApproveRejectModal}
        // isLoading={approveRejectMrfPending}
        // appliedFor={appliedFor}
        setComment={setApproveRejectComment}
        comment={approveRejectComment}
      />}
      {openProgressModel && <ProgressModel
        openModel={openProgressModel}
        handleOpenClose={onHandleProgressModel}
        progressData={progressData}
        processType={"Travel & Claim"}
      />}
      {isOpenApproveRejectClaimModal && <CommonApproveRejectModal
        title={'Travel Claim Approve Reject'}
        onApproveReject={approveRejectTravelClaim}
        isShow={isOpenApproveRejectClaimModal}
        setComment={setApproveRejectTravelClaimComment}
        comment={approveRejectTravelClaimComment}
      />}
      {isOpenTravelDetailModal && <ViewTravelDetail
        openModel={isOpenTravelDetailModal}
        handleOpenClose={toggleTravelDetail}
        travelDetailData={selectedTravelData?.travel_details?.travel_details}
      />}
      {isOpenBillDetailModal && <ViewBillDetail
        handleOpenClose={toggleBillDetail}
        openModel={isOpenBillDetailModal}
        selectedTravelClaimData={selectedTravelClaimData}
      />}
      {isOpenTravelClaimDetailModal && <ViewTravelClaimDetail
        handleOpenClose={toggleTravelClaimDetail}
        openModel={isOpenTravelClaimDetailModal}
        selectedTravelClaimData={selectedTravelClaimData}
        toggleBillDetail={toggleBillDetail}
      />}
    </div>
  )
}

export default TravelApprovalRequest
