import React, { useMemo, useState } from 'react'
import { CustomTable } from '../../reuseableComponents'
import { CustCheckbox, CustomStrong, CustRadio, Input, Label } from '../../commanComponent';
import CustomSelect from '../../commanComponent/CustomSelect';
import BlankTableError from '../../commanComponent/BlankTableError';
import { dateFormate } from '../../../utils/common';

const ViewTravelDetail = ({
    openModel,
    handleOpenClose,
    travelDetailData,
}) => {

    const renderedListOfTravel = () => {
        return (travelDetailData && travelDetailData?.length > 0 && travelDetailData?.map((item, index) => (
            <li className='select' key={index}>{renderedTravelData(item, index)}</li>

        )))
    }


    const renderedTravelData = (item, index) => {
        switch (item?.travel_type_id) {
            case "67ce8ba9bf80dc2154daa3fc":
                return (
                    <>
                        <div>
                            <div style={{
                                borderRadius: "50%",
                                padding: "4px",
                                background: "#cdcdd3e0",
                                width: "44px"
                            }}>
                                <figure className="m-0">
                                    <img src="images/airplane.png" alt="" width="35" height="35" />
                                </figure>
                            </div>
                            <div className='text-center'>Flight</div>
                        </div>
                        <div className="container ">
                            <div className="card ">
                                <div className='p-4'>
                                    <div className="d-flex justify-content-between ">
                                        <h5>Flight Detail</h5>
                                    </div>
                                    <div className="row g-3 align-items-end mt-2">
                                        <div className="col-md-4">
                                            <div className='d-flex gap-2'>
                                                <Label text={"Departure From:"} />
                                                <span>{item?.flight_departure_from}</span>
                                            </div>


                                        </div>
                                        <div className="col-md-4">

                                            <div className='d-flex gap-2'>
                                                <Label text={"Arrive At:"} />
                                                <span>{item?.flight_arrive_at}</span>
                                            </div>
                                        </div>

                                        <div className="col-md-4">
                                            <div className='d-flex gap-2'>
                                                <Label text={"Departure Date:"} />
                                                <span>{dateFormate(item?.flight_departure_date)}</span>
                                            </div>


                                        </div>
                                        <div className="col-md-4">
                                            <div className='d-flex gap-2'>
                                                <Label text={"Departure Time:"} />
                                                <span>{item?.flight_departure_time}</span>
                                            </div>


                                        </div>
                                        <div className="col-md-4">

                                            <div className='d-flex gap-2'>
                                                <Label text={"Travel Class:"} />
                                                <span>{item?.flight_travel_class}</span>
                                            </div>




                                        </div>
                                        {/* <div className="col-md-4">
                                            <Label text={"Description"} required={true} />
                                            <Input
                                                type="text"
                                                value={item?.flight_description}
                                                name="flight_description"
                                                readOnly={true}
                                            />
                                        </div> */}
                                    </div>
                                </div>

                            </div>
                        </div>


                    </>
                )
            case "67ce8aa7bf80dc2154daa32f":
                return (
                    <>
                        <div>
                            <div style={{
                                borderRadius: "50%",
                                padding: "4px",
                                background: "#cdcdd3e0",
                                width: "44px"
                            }}>
                                <figure className="m-0">
                                    <img src="images/sedan.png" alt="" width="35" height="35" />
                                </figure>
                            </div>
                            <div className='text-center'>Car</div>
                        </div>
                        <div className="container ">
                            <div className="card p-4">
                                <div className="d-flex justify-content-between mb-3">
                                    <h5>Car Detail</h5>

                                </div>

                                <div className="row  align-items-end">
                                    <div className="col-md-6 row">
                                        <Label text={"PICK-UP"} required={true} className={"mb-2"} />
                                        <div className="col-md-6">
                                            <div className='d-flex gap-2'>
                                                <CustomStrong text={"Date:"} className={"text-primary"} />
                                                <span>{dateFormate(item?.car_pick_up_date)}</span>
                                            </div>

                                        </div>
                                        <div className="col-md-6">

                                            <div className='d-flex gap-2'>
                                                <CustomStrong text={"Time:"} className={"text-primary"} />
                                                <span >{item?.car_pick_up_time}</span>
                                            </div>


                                        </div>
                                        <div className="mt-2">
                                            <div className='d-flex gap-2'>
                                                <CustomStrong text={"Address:"} className={"text-primary"} />
                                                <span >{item?.car_pick_up_address}</span>
                                            </div>

                                        </div>
                                    </div>
                                    <div className="col-md-6 row">
                                        <Label text={"DROP-OFF"} required={true} className={"mb-2"} />

                                        <div className="col-md-6">
                                            <div className='d-flex gap-2'>
                                                <CustomStrong text={"Date:"} className={"text-primary"} />
                                                <span >{dateFormate(item?.car_drop_of_date)}</span>
                                            </div>


                                        </div>
                                        <div className="col-md-6">
                                            <div className='d-flex gap-2'>
                                                <CustomStrong text={"Time:"} className={"text-primary"} />
                                                <span >{item?.car_drop_of_time}</span>
                                            </div>

                                        </div>
                                        <div className="mt-2">
                                            <div className='d-flex gap-2'>
                                                <CustomStrong text={"Address:"} className={"text-primary"} />
                                                <span >{item?.car_drop_of_address}</span>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div className="row mt-2">
                                    <div className="col-md-5 row ">

                                        <div className='d-flex gap-2 mt-3'>
                                            <Label text={"Car Type:"} required={true} className={"mb-2"} />
                                            <span >{item?.car_type}</span>
                                        </div>
                                    </div>
                                    {/* <div className="col-md-4">
                                        <Label text={"Driver Needed"} />

                                        <div>
                                            <div className="form-check form-check-inline">
                                                <CustRadio
                                                    htmlFor="yes"
                                                    text="Yes"
                                                    name="car_driver_needed"
                                                    value="yes"
                                                    checked={item?.car_driver_needed == "yes"}
                                                    readOnly={true}

                                                />
                                            </div>
                                            <div className="form-check form-check-inline">
                                                <CustRadio
                                                    htmlFor="no"
                                                    text="No"
                                                    name="car_driver_needed"
                                                    value="no"
                                                    checked={item?.car_driver_needed == "no"}
                                                    readOnly={true}


                                                />
                                            </div>
                                        </div>
                                    </div> */}

                                </div>
                            </div>
                        </div>

                    </>
                )
            case "67ce8c7fbf80dc2154daa546":
                return (
                    <>
                        <div>
                            <div style={{
                                borderRadius: "50%",
                                padding: "4px",
                                background: "#cdcdd3e0",
                                width: "44px"
                            }}>
                                <figure className="m-0">
                                    <img src="images/hotel.png" alt="" width="35" height="35" />
                                </figure>
                            </div>
                            <div className='text-center'>Hotel</div>
                        </div>
                        <div className="container ">
                            <div className="card ">

                                <div className='p-4'>
                                    <div className="d-flex justify-content-between mb-3">
                                        <h5>Hotel Detail</h5>

                                    </div>
                                    <div className="row g-3 align-items-end">

                                        <div className="col-md-6 ">

                                            <Label text={"Check-In"} required={true} />

                                            <div className='row '>

                                                <div className='col-md-6 mt-2'>
                                                    <div className='d-flex gap-2'>
                                                        <CustomStrong text={"Date:"} className={"text-primary"} />
                                                        <span >{dateFormate(item?.hotel_check_in_date)}</span>
                                                    </div>

                                                </div>
                                                <div className='col-md-6  mt-2'>
                                                    <div className='d-flex gap-2'>
                                                        <CustomStrong text={"Time:"} className={"text-primary"} />
                                                        <span >{item?.hotel_check_in_time}</span>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                        <div className="col-md-6 ">
                                            <Label text={"Check-Out"} required={true} />
                                            <div className='row '>
                                                <div className='col-md-6 mt-2'>

                                                    <div className='d-flex gap-2'>
                                                        <CustomStrong text={"Date:"} className={"text-primary"} />
                                                        <span >{dateFormate(item?.hotel_check_out_date)}</span>
                                                    </div>

                                                </div>
                                                <div className='col-md-6  mt-2'>

                                                    <div className='d-flex gap-2'>
                                                        <CustomStrong text={"Time:"} className={"text-primary"} />
                                                        <span >{dateFormate(item?.hotel_check_out_time)}</span>
                                                    </div>

                                                </div>
                                            </div>
                                        </div>
                                        {/* <div className="col-md-4">
                                            <Label text={"Description"} required={true} />
                                            <Input
                                                type="text"
                                                value={item?.hotel_drop_of_description}
                                                name="hotel_drop_of_description"
                                                readOnly={true}

                                            />
                                        </div> */}
                                        <div className="col-md-4">
                                            <div className='d-flex gap-2'>
                                                <Label text={"Location:"} />
                                                <span >{item?.hotel_location}</span>
                                            </div>

                                        </div>
                                    </div>


                                </div>

                            </div>
                        </div>
                    </>
                )
            default:
                return (
                    <>
                        <div>
                            <div style={{
                                borderRadius: "50%",
                                padding: "4px",
                                background: "#cdcdd3e0",
                                width: "44px"
                            }}>
                                <figure className="m-0">
                                    <img src="images/trevel and tour.png" alt="" width="35" height="35" />
                                </figure>
                            </div>
                            <div className='text-center'>{item?.travel_type_name}</div>
                        </div>
                        <div className="container ">
                            <div className="card ">
                                <div className='p-4'>
                                    <div className="d-flex justify-content-between ">
                                        <h5>{item?.travel_type_name}</h5>

                                    </div>
                                    <div className="row g-3 align-items-end mt-2">
                                        <div className="col-md-4">
                                            <div className='d-flex gap-2'>
                                                <Label text={"Departure From:"} />
                                                <span>{item?.departure_from}</span>
                                            </div>


                                        </div>
                                        <div className="col-md-4">

                                            <div className='d-flex gap-2'>
                                                <Label text={"Arrive At:"} />
                                                <span>{item?.arrive_at}</span>
                                            </div>



                                        </div>

                                        <div className="col-md-4">
                                            <div className='d-flex gap-2'>
                                                <Label text={"Departure Date:"} />
                                                <span>{dateFormate(item?.departure_date)}</span>
                                            </div>


                                        </div>
                                        {/* <div className="col-md-4">
                                            <Label text={"Description"} required={true} />
                                            <Input
                                                type="text"
                                                value={item?.description}
                                                name="description"
                                                readOnly={true}

                                            />
                                        </div> */}
                                    </div>
                                </div>

                            </div>
                        </div>
                    </>
                )
        }

    }

    return (
        <div>
            <div className={`offcanvas offcanvas-end  ${openModel ? 'show' : ''}`}
                tabIndex={-1}
                style={{
                    visibility: openModel ? 'visible' : 'hidden',
                    width: "800px"
                }}
                id="interviewDetail"
            >
                <div className="offcanvas-header">
                    <h5 className="offcanvas-title  fs-5 fw-bold text-primary"> Travel Detail</h5>
                    <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"
                        onClick={() => handleOpenClose()}
                    />
                </div>

                <div className="offcanvas-body myRequest ">
                    <div class="modal-body">
                        <div className="trip-itinerary">
                            <ul className="trip-detail">

                                {travelDetailData?.length > 0 ? renderedListOfTravel() : <div className='d-flex justify-content-center'> <BlankTableError text={"No Travel Detail Available"} /></div>}
                            </ul>
                        </div>
                    </div>
                </div>

            </div>
            {openModel && <div className="modal-backdrop fade show"></div>}

        </div >
    )
}

export default ViewTravelDetail
